<template>
  <div class="pt88">
    <head-bar v-bind:head="headdata" @headfun="share" @headicon="collect"></head-bar>
    <div class="heados">
      <div class="bloginfo-title">{{info.title}}</div>
      <div class="bloginfo-status">
        <div class="bloginfo-status-item">
          <img :src="icon.time" />
          <span>{{info.time}}</span>
        </div>
        <div class="bloginfo-status-item">
          <img :src="icon.status" />
          <span>{{info.status}}</span>
        </div>
      </div>
      <div class="bloginfo-img">
        <img :src="info.img" />
      </div>
      <div class="bloginfo-text" v-html="info.text"></div>
    </div>
  </div>
</template>

<script>
import headBar from '@/components/common/head'

export default {
  components: {
    headBar
  },
  name: 'blogInfo',
  data() {
    return {
      headdata: {
        src: require('@/assets/images/icon-30.png'),
        text: '',
        icon: require('@/assets/images/icon-36.png'),
        icon1: require('@/assets/images/icon-51.png')
      },
      icon: {
        time: require('@/assets/images/icon-07.png'),
        status: require('@/assets/images/icon-35.png')
      },
      info: {
        title: '设计饮食以对抗慢性炎症',
        time: 'Jan 30, 2018',
        status: 'Nutrition',
        img: require('@/assets/images/pic-07.jpg'),
        text: `<div>
                    <p style="padding:0.2rem 0 0.4rem">
                        在面对损伤和感染时，人体可以激发一系列令人震惊的反馈。大量的细胞部队陆续出动，屠杀入侵者，清除叛徒。它们的行动一般由白介素之类的信号分子指挥。白介素会告诉细胞在什么时候、去哪儿开战，什么时候战斗结束。而这场战争的外在表现，是炎症引起的肿胀、发红、疼痛，当然，这也是康复的重要组成部分。

不过，如果战争没有适时平息，炎症演变成慢性或系统性的问题，那么后果就严重了。它可能会引发关节炎、结肠炎、滑囊炎，以及糖尿病、结肠癌、阿尔茨海默病和心血管疾病等健康问题。

心血管疾病是世界上最危险的健康杀手，20年前我们就知道，炎症(以及胆固醇过高)可以造成动脉中斑块堆积。然而直到2017年夏天，人们才通过一项巧妙的大型试验理清日常控制炎症的重要性。因为失控的炎症可能引发心脏病或者中风，而让某些高风险患者使用靶向炎症药物或许可以降低出现问题的概率。哈佛大学心脏病学家保罗·里德克很久前就主张，炎症和胆固醇一样，都是重要的干预目标。

里德克的研究对象是已经有过一次心脏病发作的患者，他们中有的人出现了持续的炎症反应。不过，既然慢性炎症在心脏病等疾病中起到了不良作用，为什么不尽全力控制炎症呢？这里指的并不是吃布洛芬之类的短效药，而是在日常生活中关注每一天都能做的事：吃得正确。

在网络上，经常出现一些热门的“抗炎”饮食法和食谱。很多饮食指导瞄准了特定的疾病，比如关节炎、乳腺癌、心脏病和多种自体免疫病。还有人走得很远，甚至提出了“抗炎食物金字塔”。不过，这背后的理论却有点儿不可靠。

研究发现，有大量食物可以减轻炎症，但是，很多都不是在人体上，而是在实验室里。比如，姜黄、蓝莓、姜、茶、多种蔬菜、黑巧克力还有鱼等。南卡罗来纳大学的流行病学家詹姆斯·赫伯特和尼廷·希瓦帕调查了1943项类似的研究，在2014年发表了一份饮食炎症指数，其中涉及45种食物成分。他们创建这个指数是为了当作研究工具，评估饮食。他们也承认，这里面的研究从方法学上来讲存在着巨大的差异。

我询问里德克对抗炎指数的看法，他就变得不太自在。“这个东西突然火了起来，但是我几乎没见过什么数据可以说明哪些食物可以"抗炎"，哪些又会"促炎"。”他建议自己的患者采用地中海式饮食，多吃蔬菜、全谷食物和鱼，少吃红肉和加工食品。

地中海式饮食一直得到心脏病专家的支持，有精心设计的研究证明这种饮食能降低炎症的关键标志，并降低心脏病风险。如果地中海饮食中包含更多蓝莓和姜黄，会更有效吗？没有人知道确切的答案。

饮食研究非常微妙。姜黄可能对小鼠有抗炎作用，但芝加哥拉什大学的营养流行病学家玛莎·克莱尔·莫里斯说：“那是以啮齿类的食谱为背景的研究，整套食谱在宏观和微观营养素方面，都与人类的不同”。而食谱背景很重要。典型的地中海饮食要求每周吃大量海鲜，但是研究鱼油对人体的作用后，科学家并没有发现明确的益处。也许鱼的优点可能在其他方面，或者仅仅是因为鱼替代了其他的肉类。

这也让大量的研究人员开始把注意力放在全面的膳食结构，而不是特定的成分上。莫里斯目前正在推进一项研究，希望检查一种叫做MIND的饮食疗法，看它能否减缓认知下降。这个方案把地中海饮食的元素和另一个已经研究得很深入的饮食方案(DASH)组合在了一起。项目中也涉及了饮食与炎症之间的关系，不过至少要到2021年，才会公布结果。(撰文：克劳迪娅·沃利斯，系科学作家，《科学美国人·精神》前主编；翻译：贾明月)
                    </p>
                </div>`
      }
    }
  },
  created() {
    this.initPage()
  },
  methods: {
    initPage() {},
    share() {
      console.log('share')
    },
    collect() {
      console.log('collect')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bloginfo-title {
  font-size: 0.48rem;
  color: #282c37;
  line-height: 0.6rem;
  padding: 0 0.3rem;
  text-align: left;
}
.bloginfo-status {
  padding: 0.2rem 0.3rem;
  text-align: left;
}
.bloginfo-status-item {
  display: inline-block;
  margin-right: 0.5rem;
}
.bloginfo-status-item img {
  width: 0.25rem;
  height: 0.25rem;
}
.bloginfo-status-item span {
  font-size: 0.28rem;
}
.bloginfo-img {
  width: 100%;
  height: 4.5rem;
}
.bloginfo-text {
  text-align: left;
  font-size: 0.32rem;
  padding: 0.2rem 0.5rem;
  color: #282c37;
}
</style>



